import { Component } from '@angular/core';
import { DataFetchService } from './service/data-fetch.service';
import * as moment from 'moment';
import { FormBuilder } from '@angular/forms';
import { ILoadedEventArgs, ChartTheme } from '@syncfusion/ej2-angular-charts';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  constructor(private apiService: DataFetchService, private fb: FormBuilder) {}
  title = 'IOT-Azure-Training';
  temp: any;
  humid: any;
  tempdata: any;
  humiddata: any;
  tempStartDate: string;
  tempEndDate: string;
  data: any;
  histdata: any;
  options: any;
  public marker: object = {
    visible: true,
    height: 10,
    width: 10,
  };
  public tooltip: object = {
    enable: true,
  };

  // tslint:disable-next-line: use-lifecycle-interface
  ngOnInit(): void {
    this.apiService.getData().subscribe((res) => {
      if (res?.status === 200) {
        this.temp = res.temparature;
        this.humid = res.humidity;
      }
    });

    const param = {
      from: '2020-08-16T12:33:28.000Z',
      until: '2020-08-17T09:33:28.000Z',
    };

    this.apiService.getHistoryData(param).subscribe((res) => {
      if (res?.status === 200) {
        this.tempdata = res.temparature;
        this.humiddata = res.humidity;
      }
    });

    this.getUserDistributionGraphData();
  }

  getUserDistributionGraphData() {
    this.options = {
      chart: {
        type: 'multiBarChart',
        height: 250,
        showControls: false,
        margin: {
          top: 5,
          right: 5,
          bottom: 35,
          left: 25,
        },
        staggerLabels: false,
        duration: 500,
        stacked: false,
        groupSpacing: 0.75,
        showLegend: false,
        reduceXTicks: false,
        rotateLabels: 0,
        x(d) {
          return d.label;
        },
        y(d) {
          return d.y;
        },
        showValues: true,
        valueFormat(d) {
          return d3.format(',d')(d);
        },
        xAxis: {
          // axisLabel: 'Active User'
        },
        yAxis: {
          // axisLabel: 'Y Axis',
          axisLabelDistance: -10,
        },
      },
    };
    this.histdata = [
      {
        values: this.humiddata,
      },
    ];
    this.data = [
      {
        values: this.tempdata,
      },
    ];
    const color = ['#80d8c7', '#d4d4d4'];
    this.data.map((series, i) => {
      series.color = color[i];
      return series;
    });
  }

  public startDate(event) {
    this.tempStartDate =
      moment(new Date(event.startDate)).format('YYYY-MM-DDTh:mm:ss' + '.000') +
      'Z';
    this.tempEndDate =
      moment(new Date(event.endDate)).format('YYYY-MM-DDTh:mm:ss' + '.000') +
      'Z';
    const param = {
      from: this.tempStartDate,
      until: this.tempEndDate,
    };

    this.apiService.getHistoryData(param).subscribe((res) => {
      if (res?.status === 200) {
        this.tempdata = res.temparature;
        this.humiddata = res.humidity;
      }
    });
  }

  // custom code start
  public load(args: ILoadedEventArgs): void {
    let selectedTheme: string = location.hash.split('/')[1];
    selectedTheme = selectedTheme ? selectedTheme : 'Material';
    args.chart.theme = (
      selectedTheme.charAt(0).toUpperCase() + selectedTheme.slice(1)
    ).replace(/-dark/i, 'Dark') as ChartTheme;
  }
}
