import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class DataFetchService {
  apiURL = 'https://web20070076.azurewebsites.net/api/v1/getdata';
  apiURLHistory = 'https://web20070076.azurewebsites.net/api/v1/history';
  // ?from=2020-08-16T12:33:28.000Z&to=2020-08-17T09:33:28.000Z

  constructor(private httpClient: HttpClient) {}
  getData(): Observable<any> {
    return this.httpClient.get<any>(this.apiURL);
  }

  getHistoryData(param): Observable<any> {
    return this.httpClient.get<any>(
      this.apiURLHistory + '?from=' + param.from + '&to=' + param.until
    );
  }
}
